import { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import Ballpit from './backgrounds/Ballpit/Ballpit';
import './App.css';
import GridDistribution from './GridDistribution';

const buttonStyle = {
  padding: 'clamp(0.5em, 2vw, 0.75em) clamp(1.2em, 6vw, 2em)',
  fontSize: 'clamp(0.95rem, 2.5vw, 1.1rem)',
  fontWeight: 600,
  borderRadius: '30px',
  border: 'none',
  background: 'linear-gradient(90deg, #4f8cff 60%, #38e7ff 100%)',
  color: '#fff',
  boxShadow: '0 2px 12px #2224',
  cursor: 'pointer',
  transition: 'transform 0.15s, box-shadow 0.15s',
  outline: 'none',
};

function App() {
  useEffect(() => {
    // Dynamically add or update <meta name="viewport">
    let meta = document.querySelector('meta[name="viewport"]');
    if (!meta) {
      meta = document.createElement('meta');
      meta.name = 'viewport';
      document.head.appendChild(meta);
    }
    meta.content = 'width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no';
  }, []);

  return (
    <Router>
      <div style={{ position: 'relative', width: '100vw', height: '100vh', overflow: 'hidden' }}>
        <Ballpit className="animated-bg" />
        <div
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: '100%',
            height: '100%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: 'column',
            zIndex: 1,
            color: '#00000088',
            textShadow: '0 2px 16px #000',
            background: 'rgba(10, 16, 38, 0)',
            boxShadow: '0 0 0 100vw rgba(10, 16, 38, 0) inset',
            padding: 'clamp(1em, 4vw, 2em) 0',
            fontFamily: `'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, 'Noto Sans', sans-serif`
          }}
        >
          <div style={{ fontSize: 'clamp(1.5rem, 6vw, 2.5rem)', fontWeight: 700, letterSpacing: '2px', marginBottom: '0.5em', textAlign: 'center' }}>
            Welcome to My Animated React Page!
          </div>
          <div style={{ fontSize: 'clamp(1rem, 3vw, 1.25rem)', fontWeight: 400, marginBottom: '2em', textAlign: 'center', maxWidth: 600 }}>
            Discover awesome features, smooth animations, and a modern look. Get started by exploring the backgrounds below:
          </div>
          <div style={{ display: 'flex', gap: 'clamp(0.5em, 3vw, 1em)', flexWrap: 'wrap', justifyContent: 'center' }}>
            <Link to="/GridDistribution" style={{ textDecoration: 'none' }}>
              <button style={buttonStyle}>GridDistribution</button>
            </Link>
          </div>
        </div>
      </div>
    </Router>
  );
}

export default App;
