import Ballpit from './backgrounds/Ballpit/Ballpit';
import './App.css';

const buttonStyle = {
  padding: 'clamp(0.5em, 2vw, 0.75em) clamp(1.2em, 6vw, 2em)',
  fontSize: 'clamp(0.95rem, 2.5vw, 1.1rem)',
  fontWeight: 600,
  borderRadius: '30px',
  border: 'none',
  background: 'linear-gradient(90deg, #4f8cff 60%, #38e7ff 100%)',
  color: '#fff',
  boxShadow: '0 2px 12px #2224',
  cursor: 'pointer',
  transition: 'transform 0.15s, box-shadow 0.15s',
  outline: 'none',
};

function GridDistribution() {
  return (
    <div style={{ position: 'relative', width: '100vw', height: '100vh', overflow: 'hidden' }}>
      <Ballpit className="animated-bg" />
      <div
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexDirection: 'column',
          zIndex: 1,
          color: '#fff',
          textShadow: '0 2px 16px #000',
          background: 'rgba(10,16,38,0.65)',
          boxShadow: '0 0 0 100vw rgba(10,16,38,0.25) inset',
          padding: 'clamp(1em, 4vw, 2em) 0',
          fontFamily: `'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, 'Noto Sans', sans-serif`
        }}
      >
        <div style={{ fontSize: 'sans-serif(1.5rem, 6vw, 2.5rem)', fontWeight: 700, letterSpacing: '2px', marginBottom: '0.5em', textAlign: 'center' }}>
          React Page!
        </div>
        <div style={{ fontSize: 'clamp(1rem, 3vw, 1.25rem)', fontWeight: 400, marginBottom: '2em', textAlign: 'center', maxWidth: 600 }}>
          Discover awesome features, smooth animations, and a modern look. Get started by exploring the backgrounds below:
        </div>
        <div style={{ display: 'flex', gap: 'clamp(0.5em, 3vw, 1em)', flexWrap: 'wrap', justifyContent: 'center' }}>
          {/* <Link to="/features" style={{ textDecoration: 'none' }}>
            <button style={buttonStyle}>Features</button>
          </Link>
          <Link to="/about" style={{ textDecoration: 'none' }}>
            <button style={buttonStyle}>About</button>
          </Link>
          <Link to="/gallery" style={{ textDecoration: 'none' }}>
            <button style={buttonStyle}>Gallery</button>
          </Link>
          <Link to="/contact" style={{ textDecoration: 'none' }}>
            <button style={buttonStyle}>Contact</button>
          </Link> */}
        </div>
      </div>
    </div>
  );
}

export default GridDistribution;
